export default {
	data() {
		return {
			search_key: '',
			list: [],
			isFinish: false,
			page: 1,				
			type:1,
			loading:false,
			city:'',
			tabList:["缴费",'续费'],
			count:0,
			count1:0,
			n:0,
			start:'',
			end:''
		}
	},
	onLoad(e) {
		this.start = e.start
		this.end = e.end
		console.log(e)
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});			
		this.getData()
	},
	// 上拉加载更多
	onReachBottom(){
		if(this.list.length>19){
			uni.showLoading({
				title: '加载中',
				mask:"true"
			});
			this.loading = true
			this.page++
			this.getData()
		}
	},
	onPullDownRefresh() {
		  uni.showLoading({
		      title: '加载中',
		  	mask:"true"
		  });
		this.page = 1
		this.getData()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 2000);
	
	},
	methods: {
		getData () {
		    this.$api.month_open_renew_list({
		      search_key: this.search_key,
		      type: 1,
		      page: this.page,
			  start:this.start,
			  end:this.end
		    }).then(res => {
		      uni.hideLoading();
			  if (res.data.code === '200') {
				  // if(this.type==1){
					  this.count = res.data.data.count
				  // }else{
					 //  this.count1 = res.data.data.count
				  // }
				  
			  	if (this.page === 1) {
			  		  this.list = res.data.data.list
			  		  // console.log(this.chinaList)
			  		  this.loading = false
			  		  this.isFinish = true
			  	} else {
			  	  if(res.data.data.list.length){
			  		  setTimeout(()=>{
			  			  this.list = this.list.concat(res.data.data.list)
			  			  this.loading = false
			  			  uni.showToast({
			  				title:'加载成功',
			  				icon:"none"
			  			  })
			  		  },1500)
			  	  }else{
			  		  this.loading = false
			  		  uni.showToast({
			  			title:'没有更多的数据了',
			  			icon:"none"	,								  	
			  		  })
			  	  }	
			  	   this.isFinish = true					  
			  	}
			    console.log(this.list)
				console.log(res)
			  }
		      
		    })
		  
		},
		search () {
		  if (this.search_key.length) {
		    uni.showLoading({
		          title: '搜索中',
		      	mask:"true"
		      });
		    }
		    this.page = 1
		    this.getData()
		},
		tel(tel){
		  uni.makePhoneCall({
			phoneNumber:tel
		  })
		},
		my_info (item) {
		  uni.navigateTo({
		  	url:'../../pagesMy/MyInfo/MyInfo?id='+item.user_id
		  })
		}
		
	}
}
